import React, { useState } from 'react';
import PropTypes from 'prop-types';
import './Slider.scss';

const Slider = ({ type, subtype, steps, handleSize }) => {
  const [value, setValue] = useState(type === 'discrete' ? 0 : 50);
  const [range, setRange] = useState([20, 80]);

  const handleChange = (event) => {
    const newValue = Number(event.target.value);
    if (subtype === 'range') {
      const rangeIndex = event.target.dataset.index;
      setRange((prevRange) => {
        const newRange = [...prevRange];
        newRange[rangeIndex] = newValue;
        return newRange;
      });
    } else {
      setValue(newValue);
    }
  };

  return (
    <div className={`slider slider-${handleSize}`}>
      {subtype === 'range' ? (
        <>
          <input
            type="range"
            className="slider-input"
            min="0"
            max={type === 'discrete' ? steps : 100}
            value={range[0]}
            data-index="0"
            onChange={handleChange}
            step={type === 'discrete' ? 1 : undefined}
          />
          <input
            type="range"
            className="slider-input"
            min="0"
            max={type === 'discrete' ? steps : 100}
            value={range[1]}
            data-index="1"
            onChange={handleChange}
            step={type === 'discrete' ? 1 : undefined}
          />
          <div className="slider-values">
            {range[0]} - {range[1]}
          </div>
        </>
      ) : (
        <>
          <input
            type="range"
            className="slider-input"
            min="0"
            max={type === 'discrete' ? steps : 100}
            value={value}
            onChange={handleChange}
            step={type === 'discrete' ? 1 : undefined}
          />
          <div className="slider-values">{value}</div>
        </>
      )}
    </div>
  );
};

Slider.propTypes = {
  type: PropTypes.oneOf(['continuous', 'discrete']),
  subtype: PropTypes.oneOf(['single', 'range']),
  steps: PropTypes.number,
  handleSize: PropTypes.oneOf(['size_24', 'size_32']),
};

Slider.defaultProps = {
  type: 'continuous',
  subtype: 'single',
  steps: 10,
  handleSize: 'size_24',
};

export default Slider;
